import * as React from "react";
import { Toast } from 'antd-mobile';
import qs from 'qs';
import MainAdvertising from '../../components/advertising/mainAdvertising'
import ListenSuccess from '../../components/sign/listenSuccess'
import VipSuccess from '../../components/sign/vipSuccess'
import NormalSuccess from '../../components/sign/normalSuccess'
import Vvip from '../../components/sign/vvip'
import Fail from '../../components/sign/fail'

import logo from '../../assets/img/mine/logo.png'
import './sign.less'

import { signUp } from "../../api"

interface ISprops {
    [propsName: string]: any
}
interface IState {
    signRes: any,
    loading: boolean,
    courseId: any,
}
export default class SignIndex extends React.Component<ISprops, IState>{
    constructor(props: ISprops) {
        super(props);
        this.state = {
            signRes: {},
            loading: true,
            courseId: '',
        }
    }
    componentWillMount() {
        document.title = '签到';
        let search = this.props.location.search;
        let data = qs.parse(search.substring(1));
        this.setState({
            courseId: data.course_id,
        })
        Toast.loading('正在签到，请稍后...');
        signUp({
            courseId: data.course_id,
            signinType: 1,
        }).then(res => {
            Toast.hide()
            this.setState({
                loading: false,
                signRes: res
            })
        })
    }
    checkSuccess = (signRes,courseId) => {
        //erp_form_type 2:试听 3:补课 
        //is_club 1:vip
        // signRes.data.vip_level === 1 vvip
        //试听状态 优先级最高
        if (signRes.data.erp_form_type === 2) {
            return (
                <ListenSuccess signRes={signRes} courseId={courseId}/>
            )
        } else {
            //是否是vvip
            if (signRes.data.vip_level === 1) {
                return (
                    <Vvip signRes={signRes} courseId={courseId}/>
                )
            } else {
                if (signRes.data.is_club === 1) {
                    return (
                        <VipSuccess signRes={signRes} courseId={courseId}/>
                    )
                } else {
                    return(
                        <NormalSuccess signRes={signRes} courseId={courseId}/>
                    )
                }
            }
        }
        
    }
    render() {
        const {loading, signRes, courseId } = this.state
        return (
            <div className="sign">
                {
                    loading || (
                        signRes.status !== 0 && signRes.status !== 2005
                        ?
                        <Fail result={signRes} courseId={courseId}/>
                        :
                        this.checkSuccess(signRes,courseId)
                    )
                }
                
                <MainAdvertising title="sign-success"/>
            </div>
        )
    }

}
