import * as React from "react";
import { Link } from 'react-router-dom'


import fail from '../../assets/img/sign/fail.png'
import bind from '../../assets/img/sign/bind.png'
import failog from '../../assets/img/sign/failog.png'
import erwei from '../../assets/img/sign/erwei.jpg'
import cuo from '../../assets/img/sign/cuo.png'
import fail_head from '../../assets/img/sign/fail-head.png'
import './signcom.less'

interface ISprop {
    [propsName: string]: any
}
interface IState {
}

// 2001, "尚未关注公众号",
//     2002, "手机号未绑定"),
//    2003, "不在该课程中"),
//     2004, "课程未开始"),
export default class SignFail extends React.Component<ISprop, IState>{
    constructor(props: any) {
        super(props);
        this.state = {
        }
    }
    componentDidMount() {
    }
    checkStatus = (status) => {
        switch(status) {
            case 2001:
            return (
                <div className="normal">
                    <p className="title2">未关注公众号 <br/>高顿会员服务中心</p>
                    <img src={erwei} className="erwei" alt=""/>
                    <p className="bott">长按识别二维码</p>
                </div>
            )

            case 2002:
            return (
                <div className="normal">
                    <p className="title2">未绑定手机号</p>
                    <img src={bind} className="phone" alt=""/>
                    <Link className="bind" to="/bind/bindPhone">绑定手机号</Link>
                </div>
            )

            case 2003:
            return (
                <div className="normal">
                    <p className="title">请联系现场运营人员进行手动签到 或核实席位信息</p>
                    <div className="reason">
                        <p>可能导致签到失败原因：</p>
                        <p className="tips tips1">
                            <span>1</span>
                            绑定微信的手机号码与报名时提供的手机号<br/>不一致：
                            <Link to="/bind/bindPhone">重新绑定</Link>
                        </p>
                        <p className="tips tips2">
                            <span>2</span>
                            您所预定的席位尚未完成付款
                        </p>
                        <p className="tips tips3">
                            <span>3</span>
                            您所预定的席位不属于该班级
                        </p>
                    </div>

                    <Link to="" className="apply">在线报名</Link>
                </div>
            )

            case 2004:
            return (
                <div className="normal">
                    <p className="title2">尚未开课</p>
                    <img src={failog} className="failog" alt=""/>
                </div>
            )

            default:
            return (
                <div className="normal">
                    <p className="title">请联系现场运营人员进行手动签到 或核实席位信息</p>
                    <div className="reason">
                        <p>可能导致签到失败原因：</p>
                        <p className="tips tips1">
                            <span>1</span>
                            绑定微信的手机号码与报名时提供的手机号<br/>不一致：
                            <Link to="/bind/bindPhone">重新绑定</Link>
                        </p>
                        <p className="tips tips2">
                            <span>2</span>
                            您所预定的席位尚未完成付款
                        </p>
                        <p className="tips tips3">
                            <span>3</span>
                            您所预定的席位不属于该班级
                        </p>
                    </div>
                    <Link to={`/normalMember?courseId=${this.props.courseId}`} className="apply">在线报名</Link>
                </div>
            )
        }
    }
    render() {
        const {result} = this.props;
        return (
            <div className="fail-con">
                <div className="fail-detail">
                    <img className="con-logo" src={fail_head} alt="" />
                    <h2>
                        <img src={cuo} alt=""/>
                    </h2>
                    {
                        this.checkStatus(result.status)
                    }
                </div>
            </div>

        )
    }

}
