import * as React from "react";
import { Toast, Button } from 'antd-mobile'


import vip from '../../assets/img/sign/vip.png'
import './signcom.less'
import listen_logo from '../../assets/img/sign/listen-logo.png'
import normal_logo from '../../assets/img/sign/normal-logo.png'
import vip_logo from '../../assets/img/sign/vip-logo.png'

import listen_su from '../../assets/img/sign/listen-su.png'
import normal_su from '../../assets/img/sign/normal-su.png'
import vip_su from '../../assets/img/sign/vip-su.png'

interface ISprop {
    [propsName: string]: any
}
interface IState {
    [stateName:string]: any
}


export default class NormalSuccess extends React.Component<ISprop, IState>{
    constructor(props: any) {
        super(props);
        this.state = {
        }
    }
    componentDidMount() {
        let { result } = this.props;
        if(result.status === 2005) {
            Toast.info('您已签到')
        }
    }
    render() {
        const { result } = this.props; 
        return (
            <div className="success-con normal-con"> 
                <div className="header">
                    <img src={normal_logo} alt=""/>
                </div>
                <div className='success-detail normal_bg'>
                    <img className="con-logo" src={normal_su} alt="" />
                    <h2>签到成功</h2>
                    {
                        result.data.is_club === 1 ?
                            <img src={vip} className="vip" alt="" />
                            :
                            <div></div>
                    }
                    <p className="name">
                        {result.data.nickname}
                    </p>
                    <p className="company">
                        {result.data.company}
                    </p>
                    <p className="time">
                        {result.data.erp_form_type === 2 ? '试听' : result.data.erp_form_type === 3 ? '补课' : '' } {result.data.st_time}
                    </p>
                    <a href="/">OK</a>
                </div>
                
            </div>

        )
    }

}
